.app-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
  "header header"
  "nav content";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px; /* column-gap, row-gap */
}

.app-wrapper-content {
  grid-area: content;
  background-color: rgb(225, 229, 230);
}

button {
  background-color: #008080;
  color: #fff;
  border: none;
  text-transform: uppercase;
  outline: none;
  font-size: 14px;
  border: 2px solid #008080;
}

button:hover {
  cursor: pointer;
  background-color: #fff;
  color: #008080;
  font-weight: bold;
  border: 2px solid #008080;
}

textarea {
  border: 1px solid transparent;
  outline: none;
}

textarea:focus {
  border: 1px solid #008080;
}