.users {
    padding: 2%;
}

.user_ava img {
    width: 100px;
}

.user_ava_followed {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 10px;
}

.user_ava_followed button {
    background-color: #008080;
    font-size: 20px;
    width: 130px;
    border: none;
}

.user_ava_followed button:hover {
    cursor: pointer;
    background-color: white;
    color: #008080;
}

.user {
    padding-left: 10%;
    padding-right: 10%;
    display: flex;
    margin-bottom: 20px;
}

.about_user {
    display: flex;
    justify-content: space-between;
    border: 1px solid brown;
    border-radius: 5px;
    color: brown;
    padding: 10px;
    width: 70%;
}

.name_and_status {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.name {
    font-size: 22px;
    font-weight: bold;
}

.status {
    font-size: 18px;
    font-style: italic;
    max-width: 300px;
    word-wrap: break-word
}

.location {
    text-align: right;
    font-size: 18px;
}